:root {
  --primary-font: "Roboto", sans-serif;
  --secondery-font: "Raleway", sans-serif;
  --primary-text-color: #212121;
  --logo-color-text: #000;
  --secondery-text-color: #757575;
  --white-text-color: #fff;
  --accent-color: #2196f3;
  --button-color-accent: #188ce8;
  --button-color: #fff;
  --contact-color: rgba(255, 255, 255, .6);
  --hero-color-bg: #c4c4c4;
  --page-bg-color: #fff;
  --team-bg-color: #f5f4fa;
  --footer-bg-color: #2f303a;
  --border-color-white: #eee;
  --button-filter-bg-color: #f5f4fa;
  --logo-accent-color: #00f8d0;
  --hedaer-line-color: #ececec;
  --feature-icon-bg-color: #f5f4fa;
  --modal-input-color-: #555;
  --social-link-color: #afb1b8;
  --social-link-bg-color: rgba(255, 255, 255, .1);
  --gap-size: 30px;
  --transition-fill: fill 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-color: color 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-opacity: opacity 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-transform: transform 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-background-color: background-color 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-border: border 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-border-color: border-color 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-box-shadow: box-shadow 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--page-bg-color);
  color: var(--primary-text-color);
  font-family: var(--primary-font);
  max-height: 100vh;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

.logo {
  color: var(--logo-color-text);
  font-family: var(--secondery-font);
  letter-spacing: .03em;
  transition: var(--transition-color);
  font-size: 26px;
  font-weight: 700;
  line-height: 1.19;
}

@media screen and (min-width: 480px) {
  .logo {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}

.logo:hover, .logo:focus {
  color: var(--logo-accent-color);
  outline: rgba(0, 0, 0, 0);
}

.logo__colorized {
  color: var(--accent-color);
  transition: var(--transition-color);
}

.logo:hover .logo__colorized, .logo:focus .logo__colorized, .footer__logo:hover .logo__colorized, .footer__logo:focus .logo__colorized {
  color: var(--logo-accent-color);
  outline: rgba(0, 0, 0, 0);
}

.footer__logo {
  color: var(--white-text-color);
  letter-spacing: .03em;
  margin-bottom: 20px;
  padding: 0;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
  display: inline-block;
}

.button {
  min-width: 200px;
  max-height: 50px;
  letter-spacing: .06em;
  color: var(--button-color);
  background-color: var(--accent-color);
  cursor: pointer;
  transition: var(--transition-background-color);
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 10px 32px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button:hover, .button:focus {
  background-color: var(--button-color-accent);
  outline: rgba(0, 0, 0, 0);
}

.modal__button {
  margin-left: auto;
  margin-right: auto;
}

.singup__button {
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 28px;
}

.title {
